import { Icon } from '@iconify/react'
import styles from './technology.module.scss'

export interface TechnologyProps {
    readonly name: string
    readonly description?: string
    readonly level?: number
    readonly icon?: string
    readonly iconColor?: string
    readonly img?: string
}

function Technology({ name, description, icon, iconColor, img }: TechnologyProps) {
    return (
        <>
            <span className={styles.Technology} data-tip={description}>
                {icon && <Icon fontSize={18} icon={icon} color={iconColor} />}
                {img && <img src={img} alt={description ?? name} style={{ height: '18px' }} />}
                {(icon || img) && <> </>}
                {name}
            </span>
        </>
    )
}

export default Technology
