import { SkillInfo } from './skills-list'
import styles from './skills-list.module.scss'
import classNames from 'classnames'
import SkillsLevel from '../SkillsLevel'
import { useState } from 'react'
import _ from 'lodash'

interface SkillsListItemProps {
    readonly item: SkillInfo
}

function SkillsListItem({ item }: SkillsListItemProps) {
    const { name, description, children } = item
    const [hover, setHover] = useState(false)
    const [activeLevel, setActiveLevel] = useState<number | null>()

    const technologies = item.technologies?.map(it => (
        <span
            onMouseEnter={() => setActiveLevel(it.props.level)}
            onMouseLeave={() => setActiveLevel(null)}
        >
            {it}
        </span>
    ))

    const text = (
        <>
            {name && (
                <strong>
                    {name}
                    {(description || technologies || children) && <>:</>}{' '}
                </strong>
            )}
            {description}
            {technologies}
        </>
    )

    const itemLevel = item.level ?? Math.round(_.meanBy(item.technologies, it => it.props.level))

    if ((children?.length ?? 0) > 0) {
        return (
            <li className={styles.SkillsListItem}>
                {text}
                <ul className={styles.SkillsList}>
                    {children?.map(child => (
                        <SkillsListItem item={child} key={`${child.name}/${child.description}`} />
                    ))}
                </ul>
            </li>
        )
    } else {
        return (
            <li
                className={classNames(styles.SkillsListItem, styles.SkillsListItemHoverable)}
                onMouseOver={() => setHover(true)}
                onMouseLeave={() => setHover(false)}
            >
                <div
                    className={classNames({
                        [styles.SkillsListWithLevel]: itemLevel !== undefined,
                    })}
                >
                    <div>{text}</div>
                    <SkillsLevel
                        level={activeLevel ?? itemLevel}
                        hover={hover}
                        active={!!activeLevel}
                    />
                </div>
            </li>
        )
    }
}

export default SkillsListItem
